<template>
  <div class="heading">
    <div class="jetro">
      <h1 class="jet">JET<span class="ro" >RO</span></h1>
    </div>
    <ul>
      <li class="focus">HOME</li> 
      <li>ABOUT US</li> 
      <li>BLOG</li> 
      <li>PORFOLIO</li> 
      <li>CONTACT US</li> 
    </ul>
  </div>
</template>


<script>

export default {
  name: 'Heading',
  data () {
    return {
    }
  }
}
  
</script>

<style scoped>

.heading {
}

.jetro {
  position: absolute;
}

.jet {
  margin: 0;
  color: #e8603c;
}

.ro {
  color: #f3af9d;
}

.focus{
  color: #e8603c;
}

ul, li {
 margin: 0 0 0 1rem;
}

ul {
  position: absolute;
}

@media screen and (min-width: 1360px){
  .heading {
    position: absolute;
    text-align: center;
    height:60px;
    width: 60%;
  }
  
  .heading > *{
    position: relative;
  }

  ul {
    left: 22%;
    top: 20px;
  }
  .jetro {
    position: absolute;
    text-align: left;
  }
}

@media screen and (min-width: 1920px){
  ul {
    right: 20%;
  }
  .jetro {
    left: 20%; 
  }
}


</style>



